body {
  margin: 0;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  background-color: black;
  color: white;
  line-height: 1.5;
  overflow-x: hidden; /* Prevent horizontal scrolling on mobile */
}

/* Links */
a:hover {
  opacity: 0.7;
}

/* Headings responsive */
h1, h2, h3, h4, h5, h6 {
  margin: 0.5em 0;
}

/* Images responsive */
img {
  max-width: 100%;
  height: auto;
  display: block;
}

/* Section containers */
.section {
  padding: 40px 20px;
}

/* Media queries for mobile */
@media (max-width: 768px) {
  body {
    font-size: 16px; /* Slightly smaller text for phones */
  }

  .section {
    padding: 20px 10px;
  }

  h1 {
    font-size: 1.8em;
  }
  h2 {
    font-size: 1.5em;
  }
  p {
    font-size: 1em;
  }
}
